<template>
    <main class="main management__wrapper">
        <Preloader :is-visible="showLoaderStatus" />
        <MainTitle text="Управление подпиской" />
        <SubscribeInfo />
        <PayMethod show-card />
        <NewStories />
        <ManagementBtns />
    </main>
</template>

<script>
    import { defineAsyncComponent, defineComponent } from "vue";
    import { API } from "../components/Constants";

    export default defineComponent({
        name: "App",
        components: {
            Preloader: defineAsyncComponent(() => import("@/components/Preloader.vue")),
            MainTitle: defineAsyncComponent(() => import("@/components/MainTitle.vue")),
            SubscribeInfo: defineAsyncComponent(() => import("@/components/SubscribeInfo.vue")),
            PayMethod: defineAsyncComponent(() => import("@/components/PayMethod.vue")),
            NewStories: defineAsyncComponent(() => import("@/components/NewStories.vue")),
            ManagementBtns: defineAsyncComponent(() => import("@/components/ManagementBtns.vue")),
        },
        computed: {
            userID() {
                return this.$store.state.userInfo.USER_ID;
            },
            showLoaderStatus() {
                return this.$store.state.showLoaderStatus;
            },
            abonentType() {
                return this.$store.state.abonentInfo.abonentType;
            },
            isAuthorityAbonentType() {
                return this.$store.getters.isAuthorityAbonentType;
            }
        },
        watch: {
            userID() {
                this.$store.dispatch("sendUserEvent", "YardSubManagmentScreenOpen");
            },
            abonentType() {
                this.checkAbonentType();
            },
        },
        mounted() {
            this.checkAbonentType();
        },
        methods: {
            checkAbonentType() {
                if (this.isAuthorityAbonentType) {
                    this.$router.push(`/subscribe_vip/?token=${API.TOKEN}`);
                }
            },
        },
    });
</script>

<style>
@import "../assets/main.css";

.management__wrapper {
  height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;
  font-size: 16px;
  font-family: "SF Pro", serif;
  color: #061029;
  overflow: auto;
  box-sizing: border-box;
}

.container {
  width: calc(100% - 32px);
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
